export default class ValidateService {
    constructor(

    ) {

    }

    static validateNewUser(payload) {
        let error = null;
        const regular = /^[a-zA-Z][a-zA-Z0-9]+$/ig;
        const regular1 = /^[a-zA-Z0-9]+$/ig;

        if (payload.login.length <= 6) error = '  ';
        if (payload.login.length >= 32) error = '  ';
        if (payload.password.length <= 6) error = '  ';
        if (payload.password.length >= 32) error = '  ';
        if (!regular.test(payload.login)) error = '   ';
        if (!regular1.test(payload.password)) error = '   ';

        console.log('validator : ' + error);

        return error;
    }

    static validateUser(payload) {
        let error = null;
        const regular = /^[a-zA-Z][a-zA-Z0-9]+$/ig;
        const regular1 = /^[a-zA-Z0-9]+$/ig;

        if (payload.login.length <= 6) error = '  ';

        if (payload.login.length >= 32) error = '  ';
        if (payload.password.length <= 6) error = '  ';

        if (payload.password.length >= 32) error = '  ';
        if (!regular.test(payload.login)) error = '   ';
        if (!regular1.test(payload.password)) error = '   ';

        

        return error;
    }

    static validateCamera(payload) {
        let error = null;

        if(payload.connection_string.length <= 4) error = '   ';
        if(payload.name.length <= 4) error = '  ';

        return error;
    }

}




